import React, { useState } from 'react';

import { Link } from 'react-router-dom';
import { useEffect } from 'react';
import "./Services.css";

const Services = () => {

    const [service, setService] = useState([]);
    // const [user, setUser ] = useState({});
    useEffect(() => {
        fetch('./FakeDb.JSON')
            .then(res => res.json())
            .then(data => setService(data))
    }, [])


    // console.log (user)
    //    console.log(props.data)
    // const { title, catagories, image } = service;

    return (
        <div id="service">
            <h1>Feel Free to Choose Your Triner Right Now </h1>
            <div className="contain container">{service.map(result => <div className=" row-cols-12   ">
                <div class="col">
                    <div class="card">
                        <img src={result?.image} className="image" alt="..." />
                        <div className="card-body">
                            <p className="card-text">{result.trainer}.</p>
                            <h5 className="card-title">{result.title}</h5>
                            <small className="">{result.catagories}.</small>

                        </div>
                        <div class="card-footer">
                            <Link to={`/servicesId/${result.id}`}><button className="btn btn-primary">view details</button></Link>
                        </div>
                    </div>
                </div>


            </div>)
            }


            </div>
       </div>
    );
};

export default Services;