import React from 'react';
import "./SignUp.css"
import googleIcon from "./images/google-logo-9824.png"
import githubIcon from "./images/512x512-logo-27170.png"
import { Link } from 'react-router-dom';
import useFirebase from '../../hooks/useFirebase';
const Signup = () => {
    const { signInGoogle } = useFirebase();
    return (
        <div>
            <form action="#">
                <h2>Register</h2>
                <p>Use your Outside Account to sign in, using the     account you created on any <br /> Outside product.</p>

                <input className="input-form" placeholder="First Name" type="text" /> <br />


                <input className="input-form" placeholder="Last Name" type="text" /><br />


                <input className="input-form" placeholder="Email" type="email" /> <br />

               <input className="input-form" placeholder="Password" type="password" /><br />
                <input className="input-form" type="submit" value="submit" />
            </form>
            or
            <div>
                <button onClick={signInGoogle}><img className="loginIcon" src={googleIcon} alt="" /></button>
                <button><img className="loginIcon" src={githubIcon} alt="" /></button>


                <p>Have an account? <Link to="/login">Sign in!</Link></p></div>
        </div>
    );
}

export default Signup;