import React from 'react';
import useFirebase from '../../hooks/useFirebase';
import googleIcon from "./images/google-logo-9824.png";
import githubIcon from "./images/512x512-logo-27170.png";
import"./Login.css";
import useAuth from '../../hooks/useAuth';

const Login = () => {
    const { signInGoogle} = useAuth();
    return (
        <div>
            <form className="mt-5">
                <legend><input className="input-form" placeholder="Email" type="email" /> <br />

                    <input className="input-form" placeholder="Password" type="password" /><br />
                    <input className="input-form" type="submit" value="submit" /></legend>
        </form>
            <p>or</p> <button onClick={signInGoogle}><img className="loginIcon" src={googleIcon} alt="" /></button>
            <button><img className="loginIcon" src={githubIcon} alt="" /></button>
        </div>
    );
};

export default Login;