import React, { useState } from 'react';
import "./Home.css";
import { useEffect } from 'react';
import Services from './../Services/Services';

const Home = () => {
    const [service, setService] = useState([]);
    useEffect(() => {
        fetch('./FakeDb.JSON')
            .then(res => res.json())
            .then(data => setService(data))
    }, [])
    return (
        <div>
            <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img src="https://image.freepik.com/free-vector/organic-flat-person-meditating-lotus-position_23-2148904624.jpg" class="d-block w-100" alt="..." />
                    </div>
                    <div class="carousel-item">
                        <img src="https://image.freepik.com/free-vector/cartoon-international-day-yoga-illustration_23-2148937095.jpg" class="d-block w-100" alt="..." />
                    </div>
                    <div class="carousel-item">
                        <img src="https://image.freepik.com/free-photo/one-hiking-lifestyle-summer-yoga_1150-1002.jpg" alt="..." />
                    </div>
                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>

            </div>
            <div className="mt-5" >
                <h1 className="fw-bold">Watch Our Training in Live Action</h1>
                <Services></Services>
            </div>
            

        </div>
    );
};

export default Home;